import React, { useRef } from 'react'
import { Container } from '../globalStyle'
import Logo from '../img/logo.svg'
import { NavLink } from 'react-router-dom'
import gsap from 'gsap'
import icon from '../img/icons/swim-faq.svg'
import { timetableData } from '../data/timetableData'
import Button from '../components/elements/Button'

const Timetable = ({ setLoading }) => {
    const cursor = useRef()

    const mouseMoveHandler = (e) => {
        gsap.to(cursor.current, 0.45, {
            css: {
                left: e.clientX,
                top: e.clientY
            }
        });
    }

    const backHandler = () => {
        setLoading(false)
    }

    return (
        <div onMouseMove={(e) => mouseMoveHandler(e)} className="page">
            <div ref={cursor} className="cursor"></div>

            <Container>
                <div className="page__navbar">
                    <img src={Logo} alt="Swim System" />
                    <NavLink to={'/'} className="back-cross">
                        <button 
                            onClick={() => backHandler()} 
                            className="back-button back-button_cross">
                        </button>
                    </NavLink>
                </div>
                <div className="page__body">

                    <div className="timetable">
                    {
                        timetableData.map((day, index)=> {

                            const template = day.map((item, idx) => (
                                <div className="timetable__item" key={idx}>
                                    <div className="icon-column">
                                        <div>
                                            <img src={icon} alt="Swim System" />
                                            <time className="mobile">{item.time}</time>
                                        </div>
                                        <span className="mobile">{item.coach}</span>
                                    </div>
                                    <div className="time-column desktop">
                                        <time>{item.time}</time>
                                    </div>
                                    <div className="center-column">
                                        <div>
                                            <span className="location-name">{item.locationName}</span>
                                            <span className="location-address">{item.locationAddress}</span>
                                        </div>
                                    </div>
                                    <div className="coach-column desktop">
                                        <span>{item.coach}</span>
                                    </div>
                                    <div className="right-column">
                                        <Button to='/send' text={item.statusDescription} status={!item.status} />
                                    </div>
                                </div>
                            ))

                            switch (index) {
                                case 0: {
                                    return (
                                        <div className="timetable__block" key={index}>
                                            <div className="timetable__head">
                                                <span>ПОНЕДІЛОК</span>
                                            </div>
                                            {template}
                                        </div>
                                    )
                                }
                                case 1: {
                                    return (
                                        <div className="timetable__block" key={index}>
                                            <div className="timetable__head">
                                                <span>Вівторок</span>
                                            </div>
                                            {template}
                                        </div>
                                    )
                                }
                                case 2: {
                                    return (
                                        <div className="timetable__block" key={index}>
                                            <div className="timetable__head">
                                                <span>Середа</span>
                                            </div>
                                            {template}
                                        </div>
                                    )
                                }
                                case 3: {
                                    return (
                                        <div className="timetable__block" key={index}>
                                            <div className="timetable__head">
                                                <span>Четвер</span>
                                            </div>
                                            {template}
                                        </div>
                                    )
                                }
                                case 4: {
                                    return (
                                        <div className="timetable__block" key={index}>
                                            <div className="timetable__head">
                                                <span>п’ятниця</span>
                                            </div>
                                            
                                            {template}
                                        </div>
                                    )
                                }
                                case 5: {
                                    return (
                                        <div className="timetable__block" key={index}>
                                            <div className="timetable__head">
                                                <span>субота</span>
                                            </div>
                                            {template}
                                        </div>
                                    )
                                }
                                
                                
                                default: {
                                    return (
                                        <div>Місць немає</div>
                                    )
                                }
                            }
                        })
                    }
                    </div>

                </div>
            </Container>
        </div>
    )
}

export default Timetable
