import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import PromoSection from '../components/PromoSection'
import BeamsBlock from '../components/BeamsBlock'
import FormSection from '../components/FormSection'
import InfoSection from '../components/InfoSection'
import Slider from '../components/Slider'
import MobileMenu from '../components/MobileMenu'
import LocationSection from '../components/LocationsSection'
import GalleryBlock from '../components/GalleryBlock'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import ArticleSection from '../components/ArticleSection'
import BubblesBlock from '../components/BubblesBlock'
import Competition from '../components/Competition'
import FaqsSection from '../components/FaqsSection'
import Preloader from '../components/elements/Preloader'
import PoolSection from '../components/PoolSection'

gsap.registerPlugin(ScrollTrigger)

function Home({ loading, setLoading }) {

  const [isOpen, setIsOpen] = useState(false)
  const [isLock, setIsLock] = useState(true)
  

  const rootElement = document.documentElement;

  useEffect(() => {

    if(!loading) {
      isOpen ? setIsLock(true) : setIsLock(false)
    }
    isLock ? rootElement.classList.add('lock') : rootElement.classList.remove('lock')
    
  }, [isOpen, isLock, rootElement, loading])

  return (
    <>
      <Preloader setLoading={setLoading} loading={loading} />
      <MobileMenu
        isOpen={isOpen}
        setIsOpen={setIsOpen}
      />
      <Navbar
        menuStatus={isOpen}
        menuHandler={setIsOpen}
      />
      <div className="page-wrapper">
        <PromoSection />
        <BeamsBlock />
        <InfoSection />
        <ArticleSection />
        <GalleryBlock />
        <PoolSection />
        <Slider />
        <BubblesBlock />
        <LocationSection />
        <FaqsSection />
        <Competition />
        <FormSection />
      </div>
    </>
  );
}

export default Home;
